<template>
  <Header />

  <form submit.prevent>
    <label class="design"> New Todo</label>
    <input v-model="newTodo" name="Todo" />
    <button class="button" @click.prevent="addnewtodo">Add Todo</button>
  </form>
  <ul>
    <li v-for="(todo, index) in todos" :key="index">
      <h3 :class="{ done: todo.done }">{{ todo.content }}</h3>
      <button class="button" @click="toggleDone(todo)">Mark</button>
      <button class="button" @click="removeTodo(index)">Delete</button>
    </li>
  </ul>
</template>

<script>
import { ref } from "vue";
import Header from "./components/Header.vue";

export default {
  name: "App",
  components: {
    Header,
  },

  setup() {
    const newTodo = ref("");
    const todos = ref([]);
    function addnewtodo() {
      todos.value.push({
        done: false,
        content: newTodo.value,
      });
      newTodo.value = "";
    }
    function toggleDone(todo) {
      todo.done = !todo.done;
    }
    function removeTodo(index) {
      todos.value.splice(index, 1);
    }
    return {
      newTodo,
      addnewtodo,
      todos,
      toggleDone,
      removeTodo,
    };
  },
};
</script>

