  position: relative;
  display: inline-block;
  padding: 0.25em 0.5em;
  text-decoration: none;
  color: #fff;
  background: #0010f7; /*button color*/
  border-bottom: solid 2px #0aaefa; /*daker color*/
  border-radius: 4px;
  box-shadow: inset 0 2px 0 rgba(255, 255, 255, 0.2),
    0 2px 2px rgba(0, 0, 0, 0.19);
  font-weight: bold;
  margin: 2px;
}

.button:active {
  border-bottom: solid 2px #0aaefa;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
}
.design {
  margin: 5px;
  color: rgb(248, 12, 12);
}
</style>